import { Component, OnInit } from '@angular/core';

class Item {
  constructor(public title: string,
      public done: boolean = false,
      public price: number) { }
}

const items: Item[] = [
  { title: 'Хлеб', done: false, price: 200 },
  { title: 'Мясо', done: false, price: 599 },
  { title: 'Капуста', done: true, price: 342 }
];

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent {
  items: Item[] = items;

  additem(Bname: string, Bprice: number): void {
      event.preventDefault();
      if (Bname !== '' && Bname.trim() !== '' && Bprice != null) {
          const item: Item = new Item(Bname, false, Bprice);
          this.items.push(item);
      }
  }
}
