import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';


const str1 = 'https://query.yahooapis.com/v1/public/yql';
const str2 = '?q=select * from weather.forecast where woeid in (select woeid from geo.places(1) where text=';
const str3 = ')&format=json';


@Injectable()
export class HttpService {
  constructor(private http: HttpClient) { }

  getConfig(city: string) {
    return this.http.get(str1 + str2 + '"' + city + '"' + str3).pipe(
      map(data => {
        const query = data['query'];
        return {
          weather_city: city,
          is_sunny: query.results.channel.item.condition.text,
          rising: query.results.channel.atmosphere.rising,
          humidity: query.results.channel.atmosphere.humidity,
          wind: query.results.channel.wind.speed
        };
      })
    );
  }


}
